import React from 'react';

import './Production.scss';

import courier from '../../../assets/images/production/courier.svg';
import cow from '../../../assets/images/production/cow.svg';

function Production() {
  return (
    <section className="production">
      <div className="production__info">
        <h2 className="production__title subtitle">   10000 2</h2>
        <div className="production__text reg">
                 .  ,
           ,    .    .
        </div>
        <iframe
          className="production__video"
          width="560"
          height="315"
          src="https://www.youtube.com/embed/oqSexqqHdCQ"
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
      </div>
      <div className="production__details">
        <div className="production__details-row">
          <img src={courier} alt="" className="production__img" />
          <div className="production__subtitle">350  </div>
          <ul className="production__list reg">
            <li className="production__item">  </li>
            <li className="production__item">
              -:     
            </li>
            <li className="production__item">  + 100 </li>
            <li className="production__item">   12:00  </li>
          </ul>
        </div>
        <div className="production__details-row">
          <img src={cow} alt="" className="production__img" />
          <div className="production__subtitle">   </div>
          <ul className="production__list reg">
            <li className="production__item">  </li>
            <li className="production__item"> ,   </li>
            <li className="production__item">    </li>
            <li className="production__item">    </li>
          </ul>
        </div>
      </div>
    </section>
  );
}

export default Production;
