import React from 'react';
import styled from 'styled-components';

const Text = styled.div`
  font-weight: 400;
  margin-top: 20px;
  line-height: 1.5;
  font-family: HelveticaRegular, Arial, Helvetica, sans-serif;
  &:first-child {
    margin-top: 0;
  }
`;

const Title = styled.div`
  margin-top: 40px;
  &:first-child {
    margin-top: 0;
  }
`;

function OrderPayment() {
  return (
    <div>
      <Text>
         Level Kitchen        :
      </Text>
      <Title> </Title>
      <Text>
             ,     
        ;
      </Text>
      <Title></Title>
      <Text>
                 
            : VISA International, MasterCard Worldwide
      </Text>
    </div>
  );
}

export default OrderPayment;
