import React from 'react';
import styled from 'styled-components';

const Text = styled.div`
  margin-bottom: 30px;
`;

const Elem = styled.li`
  margin-bottom: 5px;
  list-style-type: disc;
`;

function HowMuchDelivery() {
  return (
    <div>
      <Text className="reg">  :</Text>
      <ul>
        <Elem className="reg">     100   ;</Elem>
        <Elem className="reg">-   50   ,</Elem>
        <Elem className="reg">   50   ;</Elem>
        <Elem className="reg">    +   50 ;</Elem>
        <Elem className="reg">    +   25 ;</Elem>
        <Elem className="reg">     +   25 ;</Elem>
        <Elem className="reg">--    +   25 ;</Elem>
        <Elem className="reg">
          , , , , , , , , ,  
           .
        </Elem>
      </ul>
    </div>
  );
}

export default HowMuchDelivery;
